import { APP_CONFIG } from './app.config';
import { HttpClient } from '@angular/common/http';
import { IAppConfig } from '@app/core';
import { Injectable, Inject } from "@angular/core";

import { DashboardSummaryResponseModel } from './../models/dashboard-info';


@Injectable({
  providedIn: 'root'
})
export class DashboardService {


  constructor(
    private http: HttpClient,
    @Inject(APP_CONFIG) private appConfig: IAppConfig

  ) { }

  loadDashboard() {
    const url = `${this.appConfig.apiEndpoint}/${this.appConfig.dashboardEndPoint}`;
    return this.http.get<DashboardSummaryResponseModel>(url);
  }
}
