import { DashboardSummaryResponseModel } from './../../core/models/dashboard-info';
import { Component, OnInit } from '@angular/core';
import { Observable, timer } from 'rxjs';
import { take, map } from 'rxjs/operators';
import { TokenStoreService } from "@app/core";
import { DashboardService } from '@app/core/services/dashboard.service';

@Component({
  selector: "app-protected-page",
  templateUrl: "./protected-page.component.html",
  styleUrls: ["./protected-page.component.css"]
})
export class ProtectedPageComponent implements OnInit {

  decodedAccessToken: any = {};
  accessTokenExpirationDate: Date | null = null;
  private count = 60;

  dashboardModel = <DashboardSummaryResponseModel>{};

  isLoading = false;
  updatedDateTime: Date | null = null;
  autoRefreshcounter: Observable<number> | null = null;
  jsonViewerData = {} as any;
  dadJoke = '';

  constructor(private tokenStoreService: TokenStoreService, private dashboardService: DashboardService) {


  }



  async ngOnInit() {
    this.decodedAccessToken = this.tokenStoreService.getDecodedAccessToken();
    this.accessTokenExpirationDate = this.tokenStoreService.getAccessTokenExpirationDateUtc();
    this.refreshData();

    setInterval(() => {
      this.refreshData();
    }, 1 * 60 * 1000);

  }



  private startTimer() {
    this.count = 60;
    this.autoRefreshcounter = timer(0, 1000).pipe(
      take(this.count),
      map(() => --this.count)
    );
  }
  refreshData() {

    this.isLoading = true;
    this.dashboardService.loadDashboard().subscribe(result => {
      this.dashboardModel = result;
      this.isLoading = false;
      this.updatedDateTime = new Date();
      this.startTimer();
    });

    // this.queueManagerApiService.dadJoke().subscribe(result => {
    //   this.dadJoke = result.joke;
    // });

  }


}
